<template>
    <div class="articles">
        <h1 class="tittle">{{articleShow.title}}</h1>
        <!--发表时间-->
        <div></div>
        <article v-html="articleShow.article" class="markdown-body"></article>

        <!--返回按钮-->
        <Turnbutton/>
    </div>
</template>

<script>
    import {getArticle} from "../../service/api";
    import Turnbutton from "../../components/button/Turnbutton";

    export default {
        name: "Articles",
        components: {
            Turnbutton
        },
        props: ["id"],
        data() {
            return {
                articleShow: []
            };
        },
        created() {
            let params = {
                id: this.id
            };
            //请求网络数据
            getArticle(params)
                .then(response => {
                    this.articleShow = response.data[0];
                })
                .catch(error => {
                    console.log(error);
                });
        }
    };

</script>

<style lang="scss" scoped>
    @import "../../style/_statement";
    @import "../../style/markdown.css";

    .articles {
        font-size: 16px;
        min-width: 200px;
        max-width: 980px;
        margin: 0 auto;
    }

    .tittle {
        font-size: 40px;
        margin: 0 auto;
        padding: 20px 0 10px 45px;
        border-bottom: #8c939d 3px solid;
    }

    .markdown-body {
        box-sizing: border-box;
        padding: 45px;

    }

    @include styleAllPhone {
        .markdown-body {
            padding: 15px;
        }
        .tittle {
            font-size: 25px;
            padding: 10px 0 0 15px;
        }

    }

</style>